<template>
  <div>
    <el-checkbox v-model="checked" class="pl-4 pt-3 pb-2" @change="changeCheckbox">
      <span class="text-base">是否勾选</span>
    </el-checkbox>
    <home-introduction-form ref="introductionFormRef" :checked="checked"></home-introduction-form>
    <home-upload class="pl-4" title="上传参考文献" ref="normalIntroductionRef" desc="上传：综述类文献、meta分析、医药说明、治疗指南、个案、标准等文献"></home-upload>
  </div>
</template>

<script>
import HomeIntroductionForm from "./components/HomeIntroductionForm.vue"
import HomeUpload from "./components/HomeUpload.vue"

export default {
  components: {
    HomeIntroductionForm,
    HomeUpload,
  },
  data() {
    return {
      checked: false,
    }
  },
  methods: {
    changeCheckbox(value) {
      this.$refs.introductionFormRef.resetRequiredForm(value)
      this.$refs.normalIntroductionRef.resetRequiredFile(value)
    },
  },
}
</script>
