<template>
  <div>
    <el-form label-position="top" label-width="80px" :rules="rules" ref="profileForm" :model="form"
      class="flex flex-wrap">
      <el-form-item label="机构" prop="institution" class="w-2/4 pl-4">
        <el-input v-model="form.institution" placeholder="例如：北京协和医院" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="病例数据时间" prop="caseDataTime" class="w-2/4 pl-4">
        <el-input v-model="form.caseDataTime" placeholder="例如：2014年1月-2022年6月" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="目标治疗方法（暴露组）" prop="targetTherapy" class="w-2/4 pl-4">
        <el-input v-model="form.targetTherapy" placeholder="例如：冷圈套息肉切除术(CSP)" maxlength="100" @change="changeTargetTherapy" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="暴露组病例数" prop="caseNumber" class="w-2/4 pl-4">
        <el-input v-model="form.caseNumber" placeholder="例如：120" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="对照组治疗方法（非暴露组）" prop="treatmentMethod" class="w-2/4 pl-4">
        <el-input v-model="form.treatmentMethod" placeholder="例如：热圈套息肉切除术(HSP)" maxlength="100" :disabled="!checked" @change="changeTreatmentMethod"></el-input>
      </el-form-item>
      <el-form-item label="非暴露组病例数" prop="experimentalGroupSize" class="w-2/4 pl-4">
        <el-input v-model="form.experimentalGroupSize" placeholder="例如：110" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="纳入标准" prop="inclusionCriteria" class="w-2/4 pl-4">
        <el-input v-model="form.inclusionCriteria" placeholder="年龄要求：20-65岁，性别要求:无要求，符合:结肠息肉标准" :disabled="!checked"
          maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="数据收集方法" prop="dataCollectionMethod" class="w-2/4 pl-4">
        <el-input v-model="form.dataCollectionMethod" placeholder="例如：北京协和医院住院管理系统" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="伦理审查号" prop="ethicalReviewNumber" class="w-2/4 pl-4">
        <el-input v-model="form.ethicalReviewNumber" placeholder="例如：4564364374" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="排除标准" prop="exclusionCriteria" class="w-2/4 pl-4">
        <el-input v-model="form.exclusionCriteria" placeholder="孕期、年龄过大、并发症过多，死亡、高血压、病例中缺失值过多" :disabled="!checked"
          maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="统计工具" prop="statisticalTool" class="w-2/4 pl-4">
        <el-input v-model="form.statisticalTool" placeholder="例如：R语言" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="缺失值处理工具" prop="missingProcessingTool" class="w-2/4 pl-4">
        <el-input v-model="form.missingProcessingTool" placeholder="例如：Python的sklearn包" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="缺失值处理方法" prop="missingHandlingMethod" class="w-full pl-4">
        <el-input v-model="form.missingHandlingMethod"
          placeholder="例如：一般资料年般、临床强标采用均值进行填补;性别采用众数填补。观察指标不允许缺失，即:包含缺失致医的样本直接放弃" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        institution: "",
        caseDataTime: "",
        targetTherapy: "",
        caseNumber: "",
        treatmentMethod: "",
        experimentalGroupSize: "",
        inclusionCriteria: "年龄要求：20-65岁，性别要求:无要求，符合:结肠息肉标准",
        dataCollectionMethod: "",
        ethicalReviewNumber: "",
        exclusionCriteria: "孕期、年龄过大、并发症过多，死亡、高血压、病例中缺失值过多",
        statisticalTool: "R语言",
        missingProcessingTool: "Python的sklearn包",
        missingHandlingMethod: "一般资料年般、临床强标采用均值进行填补;性别采用众数填补。观察指标不允许缺失，即:包含缺失致医的样本直接放弃",
      },
      rules: {
        institution: [{ required: false, message: "请输入机构", trigger: "blur" }],
        caseDataTime: [{ required: false, message: "请输入病例数据时间", trigger: "blur" }],
        targetTherapy: [{ required: false, message: "请输入目标治疗方法（暴露组）", trigger: "blur" }],
        caseNumber: [{ required: false, message: "请输入暴露组病例数", trigger: "blur" }],
        treatmentMethod: [{ required: false, message: "请输入对照组治疗方法（非暴露组）", trigger: "blur" }],
        experimentalGroupSize: [{ required: false, message: "请输入非暴露组病例数", trigger: "blur" }],
        inclusionCriteria: [{ required: false, message: "请输入纳入标准", trigger: "blur" }],
        dataCollectionMethod: [{ required: false, message: "请输入数据收集方法", trigger: "blur" }],
        ethicalReviewNumber: [{ required: false, message: "请输入伦理审查号", trigger: "blur" }],
        exclusionCriteria: [{ required: false, message: "请输入排除标准", trigger: "blur" }],
        statisticalTool: [{ required: false, message: "请输入统计工具", trigger: "blur" }],
        missingProcessingTool: [{ required: false, message: "请输入缺失值处理工具", trigger: "blur" }],
        missingHandlingMethod: [{ required: false, message: "请输入缺失值处理方法", trigger: "blur" }],
      }
    }
  },
  methods: {
    resetRequiredForm(value) {
      this.rules.institution[0].required = value
      this.rules.caseDataTime[0].required = value
      this.rules.targetTherapy[0].required = value
      this.rules.caseNumber[0].required = value
      this.rules.treatmentMethod[0].required = value
      this.rules.experimentalGroupSize[0].required = value
      this.rules.inclusionCriteria[0].required = value
      this.rules.dataCollectionMethod[0].required = value
      this.rules.ethicalReviewNumber[0].required = value
      this.rules.exclusionCriteria[0].required = value
      this.rules.statisticalTool[0].required = value
      this.rules.missingProcessingTool[0].required = value
      this.rules.missingHandlingMethod[0].required = value
    },
    changeTargetTherapy(value) {
      this.$bus.emit('fromTargetTherapy', value)
    },
    changeTreatmentMethod(value) {
      this.$bus.emit('fromTreatmentMethod', value)
    },
  },
  created() {
    this.$bus.on("fromTargetTherapy", data => {
      this.form.targetTherapy = data
    })
    this.$bus.on("fromTreatmentMethod", data => {
      this.form.treatmentMethod = data
    })
  }
}
</script>
