<template>
  <div>
    <div class="pt-2 pb-4"><span class="text-[#F56C6C] mr-1" v-show="isValidate">*</span>{{ title }}</div>
    <el-upload class="upload-demo" accept=".pdf, .doc, .docx, .xls, .xlsx" drag :action="uploadUrl" :headers="headerObj"
      multiple :show-file-list="false" :on-success="handleSuccess" :auto-upload="true">
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">点击或拖拽文件到此区域上传</div>
      <div class="mt-6">
        <el-button type="primary" size="medium">点击上传</el-button>
        <el-button size="medium" @click.stop="onDownload" class="bg-[#eee]" v-if="showDownloadBtn">下载模版</el-button>
      </div>
      <div class="m-4 text-[#a9aeb7]" v-if="desc">{{ desc }}</div>
      <div class="mb-2 text-[#a9aeb7]" v-if="countContent">{{ countContent }}</div>
    </el-upload>
    <div class="flex flex-wrap mx-[-6px] pt-2">
      <div class="w-2/6 p-[6px]" v-for="(item, i) of list" :key="i">
        <el-card shadow="always" class="relative">
          <div class="flex items-center">
            <div>
              <a :href="item.url" target="_Blank" class="mr-2"><img class="h-[40px]" src="@/assets/word.png"
                  v-if="getFileExtension(item.url) == 'docx' || getFileExtension(item.url) == 'doc'" /></a>
              <a :href="item.url" target="_Blank" class="mr-2"><img class="h-[40px]" src="@/assets/excel.png"
                  v-if="getFileExtension(item.url) == 'xlsx' || getFileExtension(item.url) == 'xls'" /></a>
              <a :href="item.url" target="_Blank" class="mr-2"><img class="h-[40px]" src="@/assets/pdf.png"
                  v-if="getFileExtension(item.url) == 'pdf'" /></a>
            </div>
            <div class="flex-1 overflow-hidden">
              <div class="break-all truncate">{{ item.name }}</div>
              <div class="mt-2 text-xs text-[#999]">{{ item.size }}</div>
            </div>
          </div>
          <div class="absolute right-[-6px] top-[-6px]">
            <img class="w-[20px]" src="@/assets/remove.png" @click="onDelete(i)" />
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
import { bytesToSize } from "@/utils/validate"
export default {
  props: {
    title: { type: String, default: "" },
    showDownloadBtn: { type: Boolean, default: false },
    desc: { type: String, default: "" },
    templateUrl: { type: String, default: "" },
    countContent: { type: String, default: "" }
  },
  data() {
    return {
      uploadUrl: "https://dev.smileteeth.cn/supportapi/upload/file",
      headerObj: {
        Authorization:
          "eyJhbGciOiJIUzI1NiIsInppcCI6IkRFRiJ9.eNqqVgoN9XRRslIyNVfSUcosLgYyU1LL9IpzM3NSS1JTSzL0kvOAMqF-jr6uQDlVVzNVCwtVC1cQw9JS1dJY1dVS1clS1cINqCq1okDJytDcyNTM1MLYxFBHKS8pDSJgamEGFKgFAAAA__8.eKhwj57AD3CG4QdXPP8eCz0qQlPyakWU4XFAi6IyhDw",
      },
      list: [],
      isValidate: false,
    }
  },
  methods: {
    resetRequiredFile(isValidate) {
      this.isValidate = isValidate
    },
    getFileExtension(filename) {
      return /[.]/.exec(filename) ? /[^.]+$/.exec(filename)[0] : ""
    },
    getFileList(response, res) {
      const list = []
      list.push({
        name: res.name,
        url: response.data,
        size: bytesToSize(res.size),
      })
      return list
    },
    handleSuccess(response, res) {
      if (response.code == "000000") {
        this.list = this.list.concat(this.getFileList(response, res))
      } else {
        this.$message.error(response.codeDesc)
      }
    },
    onDelete(i) {
      this.list.splice(i, 1)
    },
    onDownload() {
      const url = this.templateUrl
      const fileExtension = this.getFileExtension(url)
      const name = this.title + "模板." + fileExtension
      this.downloadFileFromApi(url, name)
    },
    async downloadFileFromApi(fileUrl, fileName) {
      try {
        const response = await fetch(fileUrl)
        const blob = await response.blob()
        const url = URL.createObjectURL(blob)

        const link = document.createElement("a")
        link.href = url
        link.setAttribute("download", fileName)
        document.body.appendChild(link)
        link.click()

        // 清理工作
        document.body.removeChild(link)
        URL.revokeObjectURL(url)
      } catch (error) {
        console.error("下载模板失败:", error)
      }
    },
  },
}
</script>
