import axios from 'axios'
import { Message } from 'element-ui'

// create an axios instance
const service = axios.create({
  baseURL: process.env.VUE_APP_BASE_API, // url = base url + request url
  timeout: 5000 // request timeout
})

// request interceptor
service.interceptors.request.use(
  config => {
    config.headers['Authorization'] = "eyJhbGciOiJIUzI1NiIsInppcCI6IkRFRiJ9.eNqqVgoN9XRRslIyNVfSUcosLgYyU1LL9IpzM3NSS1JTSzL0kvOAMqF-jr6uQDlVVzNVCwtVC1cQw9JS1dJY1dVS1clS1cINqCq1okDJytDcyNTEwtTE3ExHKS8pDSpgaAwUqAUAAAD__w.n_lkNbhI8K2ajIrc6mGdx8KpeG2zIVQs8nxZIUcJEzk"
    return config
  },
  error => {
    console.log(error) // for debug
    return Promise.reject(error)
  }
)

// response interceptor
service.interceptors.response.use(
  response => {
    const res = response.data
    if (res.code !== "000000") {
      Message({
        message: res.codeDesc || 'Error',
        type: 'error',
        duration: 5 * 1000
      })
      return Promise.reject(new Error(res.codeDesc || 'Error'))
    } else {
      return res
    }
  },
  error => {
    console.log('err' + error) // for debug
    Message({
      message: error.codeDesc,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  }
)

export default service