<template>
  <div>
    <el-form label-position="top" label-width="80px" :rules="rules" ref="introductionForm" :model="form"
      class="flex flex-wrap">
      <el-form-item label="大疾病背景" prop="seriousDisease" class="w-2/4 pl-4">
        <el-input v-model="form.seriousDisease" placeholder="例如：息肉" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="小疾病背景" prop="indisposition" class="w-2/4 pl-4">
        <el-input v-model="form.indisposition" placeholder="例如：结直肠息肉" maxlength="100" :disabled="!checked"
          @change="changeIndisposition"></el-input>
      </el-form-item>
      <el-form-item label="具体研究疾病" prop="illnessName" class="w-2/4 pl-4">
        <el-input v-model="form.illnessName" placeholder="例如：微小型结直肠息肉" maxlength="100" :disabled="!checked"
          @change="changeIllnessName"></el-input>
      </el-form-item>
      <el-form-item label="目标治疗方法（暴露组）" prop="targetTherapy" class="w-2/4 pl-4">
        <el-input v-model="form.targetTherapy" placeholder="例如：冷圈套息切除术（CSP）" maxlength="100" :disabled="!checked"
          @change="changeTargetTherapy"></el-input>
      </el-form-item>
      <el-form-item label="创新点" class="w-2/4 pl-4">
        <el-input v-model="form.innovationPoint" placeholder="例1：微小型结肠息肉这个患者群体 例 2：CSP用于治疗微小型结肠息" maxlength="100" :disabled="!checked"
          @change="changeInnovationPoint"></el-input>
      </el-form-item>
      <el-form-item label="对照组治疗方法（非暴露组）" prop="treatmentMethod" class="w-2/4 pl-4">
        <el-input v-model="form.treatmentMethod" placeholder="例如：热圈套息肉切除术（HSP）" maxlength="100" :disabled="!checked"
          @change="changeTreatmentMethod"></el-input>
      </el-form-item>
      <el-form-item label="论文题目" class="w-full pl-4">
        <el-input v-model="form.paperTitle" placeholder="AI生成论文题目" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="机构" prop="organization" class="w-full pl-4">
        <el-input v-model="form.organization" placeholder="例如：北京协和医院" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="已知的治疗方法" prop="knownTreatments" class="w-full pl-4">
        <el-input v-model="form.knownTreatments" placeholder="例如：镜下切除术、内镜下黏膜切除术、内镜下黏膜剥离术、外科手术治疗、药物治疗" :disabled="!checked"
          maxlength="100"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    checked: { type: Boolean, default: false },
  },
  data() {
    return {
      form: {
        seriousDisease: "",
        indisposition: "",
        illnessName: "",
        targetTherapy: "",
        innovationPoint: "",
        treatmentMethod: "",
        paperTitle: "",
        organization: "",
        knownTreatments: ""
      },
      rules: {
        seriousDisease: [{ required: false, message: "请输入大疾病背景", trigger: "blur" }],
        indisposition: [{ required: false, message: "请输入小疾病背景", trigger: "blur" }],
        illnessName: [{ required: true, message: "请输入具体研究疾病", trigger: "blur" }],
        targetTherapy: [{ required: true, message: "请输入目标治疗方法（暴露组）", trigger: "blur" }],
        treatmentMethod: [{ required: true, message: "请输入对照治疗方法（非暴露组）", trigger: "blur" }],
      },
    }
  },
  methods: {
    resetRequiredForm(value) {
      this.rules.seriousDisease[0].required = value
      this.rules.indisposition[0].required = value
    },
    getPaperTitle() {
      this.form.paperTitle = this.form.treatmentMethod && this.form.illnessName ? this.form.treatmentMethod + "治疗" + this.form.illnessName + "疗效评估" : ""
    },
    changeIllnessName(value) {
      this.$bus.emit('fromIllnessName', value)
      this.getPaperTitle()
    },
    changeIndisposition(value) {
      this.$bus.emit('fromIndisposition', value)
    },
    changeTargetTherapy(value) {
      this.$bus.emit('fromTargetTherapy', value)
    },
    changeInnovationPoint(value) {
      this.$bus.emit('fromInnovationPoint', value)
    },
    changeTreatmentMethod(value) {
      this.getPaperTitle()
      this.$bus.emit('fromTreatmentMethod', value)
    },
  },
  created() {
    this.$bus.on("fromIndisposition", data => {
      this.form.indisposition = data
    })
    this.$bus.on("fromTargetTherapy", data => {
      this.form.targetTherapy = data
    })
    this.$bus.on("fromTreatmentMethod", data => {
      this.form.treatmentMethod = data
    })
    this.$bus.on("fromIllnessName", data => {
      this.form.illnessName = data
      this.getPaperTitle()
    })
    this.$bus.on("fromInnovationPoint", data => {
      this.form.innovationPoint = data
    })
  }
}
</script>
