      <span class="text-base">是否勾选</span>
    </el-checkbox>
    <home-discuss-form ref="discussFormRef" :checked="checked"></home-discuss-form>
    <home-upload class="pl-4" title="疗效对比表" :showDownloadBtn="true" ref="discussMedicalUploadRef" :templateUrl="medicalTemplate"></home-upload>
    <home-upload class="pl-4" title="上传参考文献" ref="discussFileRef" desc="上传与治疗方法、疾病机理、及评估结果解释相关的文献" countContent="大约 5-10 篇"></home-upload>
  </div>
</template>

<script>
import HomeDiscussForm from "./components/HomeDiscussForm.vue"
import HomeUpload from "./components/HomeUpload.vue"

export default {
  components: {
    HomeDiscussForm,
    HomeUpload,
  },
  data() {
    return {
      checked: false,
      medicalTemplate: "https://space-uat.oss-cn-shenzhen.aliyuncs.com/supports/202409/20240906064114391.42816432972654.docx",
    }
  },
  methods: {
    changeCheckbox(value) {
      this.$refs.discussFormRef.resetRequiredForm(value)
      this.$refs.discussMedicalUploadRef.resetRequiredFile(value)
      this.$refs.discussFileRef.resetRequiredFile(value)
    },
  },
}
</script>
