<template>
  <div>
    <el-form label-position="top" label-width="80px" :rules="rules" ref="discussForm" :model="form"
      class="flex flex-wrap">
      <el-form-item label="小疾病背景" prop="indisposition" class="w-2/4 pl-4">
        <el-input v-model="form.indisposition" placeholder="例如：结直肠息肉" maxlength="100" @change="changeIndisposition" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="具体研究疾病" prop="illnessName" class="w-2/4 pl-4">
        <el-input v-model="form.illnessName" placeholder="例如：微小型结肠息肉" maxlength="100" @change="changeIllnessName" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="目标治疗方法（暴露组）" prop="targetTherapy" class="w-2/4 pl-4">
        <el-input v-model="form.targetTherapy" placeholder="例如：冷切套息肉切除术(CSP)" maxlength="100" @change="changeTargetTherapy" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="对照组治疗方法（非暴露组）" prop="treatmentMethod" class="w-2/4 pl-4">
        <el-input v-model="form.treatmentMethod" placeholder="例如：热切套息肉切除术(HSP)" maxlength="100" @change="changeTreatmentMethod" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="未来研究方向" prop="futureDirection" class="w-full pl-4">
        <el-input v-model="form.futureDirection" placeholder="例如：患者的术后用药、护理、日常管理:术后生活质量" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="创新点" prop="innovationPoint" class="w-full pl-4">
        <el-input v-model="form.innovationPoint" placeholder="例如：微小型结直肠息肉患者" maxlength="100" @change="changeInnovationPoint" :disabled="!checked"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    checked: { type: Boolean, default: false },
  },
  data() {
    return {
      form: {
        indisposition: "",
        illnessName: "",
        targetTherapy: "",
        treatmentMethod: "",
        futureDirection: "患者的术后用药、护理、日常管理:术后生活质量",
        innovationPoint: "微小型结直肠息肉患者",
      },
      rules: {
        indisposition: [{ required: false, message: "请输入小疾病背景", trigger: "blur" }],
        illnessName: [{ required: false, message: "请输入具体研究疾病", trigger: "blur" }],
        targetTherapy: [{ required: false, message: "请输入目标治疗方法（暴露组）", trigger: "blur" }],
        treatmentMethod: [{ required: false, message: "请输入对照治疗方法（非暴露组）", trigger: "blur" }],
        futureDirection: [{ required: false, message: "请输入未来研究方向", trigger: "blur" }],
        innovationPoint: [{ required: false, message: "请输入创新点", trigger: "blur" }],
      },
    }
  },
  methods: {
    resetRequiredForm(value) {
      this.rules.indisposition[0].required = value
      this.rules.illnessName[0].required = value
      this.rules.targetTherapy[0].required = value
      this.rules.treatmentMethod[0].required = value
      this.rules.futureDirection[0].required = value
    },
    changeIndisposition(value) {
      this.$bus.emit('fromIndisposition', value)
    },
    changeTargetTherapy(value) {
      this.$bus.emit('fromTargetTherapy', value)
    },
    changeTreatmentMethod(value) {
      this.$bus.emit('fromTreatmentMethod', value)
    },
    changeIllnessName(value) {
      this.$bus.emit('fromIllnessName', value)
    },
    changeInnovationPoint(value) {
      this.$bus.emit('fromInnovationPoint', value)
    },
  },
  created() {
    this.$bus.on("fromTargetTherapy", data => {
      this.form.targetTherapy = data
    })
    this.$bus.on("fromTreatmentMethod", data => {
      this.form.treatmentMethod = data
    })
    this.$bus.on("fromInnovationPoint", data => {
      this.form.innovationPoint = data
    })
    this.$bus.on("fromIllnessName", data => {
      this.form.illnessName = data
    })
    this.$bus.on("fromIndisposition", data => {
      this.form.indisposition = data
    })
  }
}
</script>
